<template>
  <div>
    <div class="order">
      <div class="menu-list"
           ref="menu">
        <ul>
          <li class="menu"
              :class="{'active':activeIndex==index}"
              v-for="(obj,index) in list"
              :key="obj.id"
              @click='goToList(index)'>
            {{obj.name}}</li>
        </ul>
        <!-- 左侧山商品列表 -->
      </div>
      <!-- 右侧内容列表 -->
      <div class="el-list"
           ref="elData">
        <ul>
          <!-- 第一次循环标题 -->
          <li class="el-title"
              v-for="(obj,index) in list"
              :key="index">
            <div class="title">{{obj.name}}</div>
            <ul>
              <!-- 循环单个商品 -->
              <li class="el-box"
                  v-for="(item,Tindex) in obj.content"
                  :key="Tindex">
                <div class="img-box">
                  <img :src="item.img"
                       alt="">
                </div>
                <div class="info">
                  <div class="name">{{item.name}}</div>
                  <div class="sale">
                    <span class="num">{{item.num}}月销</span>
                    <span class="up">{{item.up}}赞</span>
                  </div>

                  <div class="price">¥ {{item.price}}</div>
                </div>
                <div class="bt-cart"></div>
                <addCart class="add-cart"
                         :index="index"
                         :type='Tindex'></addCart>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import { mapState } from 'vuex'
import addCart from '@/components/cart.vue'

export default {
  data () {
    return {
      menuScroll: null,
      elScroll: null,
      // 设定改变menu是css的标记
      activeIndex: 0,
      // 创建两个储存menu和list的列表
      posy: [],//高度
      menuEl: [],//元素
      // 保存当前滑动的距离
      scrollY: 0
    }
  },
  methods: {
    // 初始化组件
    initScroll () {
      this.menuScroll = new BScroll('.menu-list', {
        bounce: false,
      })
      this.elScroll = new BScroll('.el-list', {
        bounce: false,
        probeType: 3
      })
      // 计算出list的高度
      this.getPosY()
      // 计算滚动距离
      this.menuEl = this.$refs.menu.getElementsByClassName('menu');
      // 监听scroll组件的滚动，每次会把返回值取整给滚动高度
      this.elScroll.on('scroll', e => {
        this.scrollY = Math.abs(Math.round(e.y));
      })
    },
    // 点击菜单滚动列表
    goToList (index) {
      let elList = this.$refs.elData.getElementsByClassName('el-title')
      // 拉取全部商品类而不是所有商品
      let el = elList[index]
      // 进行list的滚动，并更改标记css
      this.elScroll.scrollToElement(el, 300)
      this.activeIndex = index
    },
    getPosY () {
      let elList = this.$refs.elData.getElementsByClassName('el-title')
      let y = 0;
      // 将list的每个商品类的高度获取并保存在列表
      for (let i = 0; i < elList.length; i++) {
        if (i == 0) this.posy.push(y)
        else {
          let upEl = elList[i - 1]
          y += upEl.offsetHeight
          this.posy.push(y)
        }
      }

    }
  },
  components: {
    addCart
  },
  computed: {
    ...mapState('storeList', ['list'])
    // 映射后台数据
  },
  created () {
    // 让后台所有数据都加载好后开始计算scroll插件所需要的值
    this.$store.dispatch('storeList/getListData', this.$route.query.id).then(() => {
      this.$nextTick(() => {
        this.initScroll()
      })
    })
  },
  watch: {
    // 监听属性，每次属性改变会触发
    scrollY (val) {
      for (let i = 0; i < this.posy.length; i++) {
        let x = this.posy[i]
        let y = this.posy[i + 1]
        if (val >= x && y > val) {
          let el = this.menuEl[i]
          this.menuScroll.scrollToElement(el, 300, 0, 100)
          // 注意要把标记重定位不然，没办法更改样式
          this.activeIndex = i
          // 返回节省内存
          break
        }
      }
    }
  }
}

</script>

