<template>
  <div ref="commentBox"
       class="commentBox">
    <div class="uio">
      <titles :delivery="comment.delivery"
              :score="comment.score"
              :packing="comment.packing"
              :flavor="comment.flavor"></titles>
      <tag :tag="comment.label"
           :currentType="type"
           @change="change"></tag>
      <list :list="comment.rate"></list>
    </div>
  </div>
</template>

<script>
import titles from './sTitle'
import list from './list'
import tag from './tag'
// api
import { getComment } from '@/utils/api.js'
import BScroll from 'better-scroll'
export default {
  data () {
    return {
      type: 1, //全部
      comment: {},
      commentScroll: null
    }
  },
  components: {
    titles,
    list,
    tag
  },
  methods: {
    getData () {
      return new Promise(reslove => {
        getComment({ id: this.$route.query.id, type: this.type }).then(res => {
          this.comment = res.data
          reslove()
        })
      })
    },
    change (id) {
      this.type = id,
        this.getData()
    }
  },
  created () {
    this.getData().then(() => {
      this.$nextTick(() => {
        this.commentScroll = new BScroll(this.$refs.commentBox, {
          bounce: false
        })
      })
    })
  }
}
</script>

<style lang="scss" scoped>
.commentBox {
  height: calc(100vh - 44px);
  overflow: hidden;
}
</style>