<template>
  <div>
    <van-list v-model="loading"
              :finished="finished"
              finished-text="没有更多了"
              @load="getData">
      <ul class="shop-list">
        <li class="list-box"
            v-for="shop in list"
            :key='shop.id'
            @click="goShell(shop.id)">
          <div class="img-box">
            <img :src="shop.img"
                 alt="">
          </div>
          <div class="info-box">
            <div class="title">{{shop.name}}</div>
            <div class="text">
              <div>
                <!-- <van-rate :v-model="4.2"
                        :size="12"
                        color="#ffd21e"
                        void-icon="star"
                        void-color="#eee"
                        class-prefix='icon' /> -->
                <!-- vant 做星级还有点问题 -->
                <star :score='shop.score'></star>
                <span class="airs">{{shop.score}}</span>
                <span>月销:{{shop.num}}</span>
              </div>
              <div>
                <span>{{shop.minute}}分钟</span>
                <span class="left-line">{{shop.distance}}</span>
              </div>
            </div>
            <div class="detail">
              <span>时间{{shop.minute}}</span>
              <span class="left-line">距离: {{shop.distance}}</span>
              <span class="left-line">起送费: {{shop.price}}</span>
            </div>
          </div>
        </li>
      </ul>

    </van-list>

  </div>
</template>

<script>
import { GetStore } from '@/utils/api.js'
import star from '../../components/star'
export default {
  data () {
    return {
      // 参数
      num: 0,
      size: 10,
      list: [],
      finished: false,
      loading: false,
      total: 0

    }
  },
  components: {
    star
  },

  methods: {
    getData () {
      GetStore({ current: this.num, size: this.size }).then(res => {
        this.list = this.list.concat(res.data.list);
        this.loading = false
        this.num++;
        this.total = res.data.total;
        if (this.list.length >= this.total) this.finished = true;

      })
    },
    goShell (id) {
      this.$router.push({ path: '/detail', query: { id } })
    }
  }

}
</script>

