<template>
  <div>
    <div class="cart-box">
      <div class="content">
        <div class="icon"
             :class="{active:sPrice}"
             @click="flge=!flge">
          <span class="iconfont icon-gouwuche1 ifont"></span>
          <span class="num"
                v-show="Snum">{{Snum}}</span>
        </div>
        <div class="cart-info"
             :class="{active:sPrice}"
             @click="flge=!flge">
          <div class="price">¥ {{sPrice}}</div>
          <div class="sub">另需配送费{{data.fee}}</div>
        </div>
        <div class="buybt"
             :class="{active:data.price<sPrice}"
             @click="goBut(sPrice)">
          {{buy}}
        </div>
      </div>
      <div class="ball-box">
        <div v-for="(el,index) in ballList"
             :key="index">
          <transition @before-enter="beforeEnter"
                      @enter="enter"
                      @after-enter="afterEnter">
            <div class="ball"
                 v-show='el.show'>

              <div class="inner"></div>
            </div>
          </transition>
        </div>
      </div>
      <transition name='fate'>
        <div class="shop-list"
             v-show="flge && Snum">
          <div class="top-bt">
            <span @click="delList()">清空购物车</span>
          </div>
          <ul class="cont-box">
            <li class="shop"
                v-for="boj in seller"
                :key="boj.id">
              <span class="name">{{boj.name}}</span>
              <span class="price">${{boj.price}}</span>
              <addCart :index='boj.index'
                       :type='boj.type'></addCart>
            </li>
          </ul>
        </div>
      </transition>
    </div>
    <div class="mask"
         v-show="flge && Snum"
         @click="flge=!flge"></div>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import addCart from '@/components/cart.vue'

export default {
  props: ['data'],
  computed: {
    ...mapGetters('storeList', ['sPrice', 'Snum', 'seller']),
    ...mapState('ball', ['ballList']),
    buy () {
      if (this.sPrice == 0) return `${this.data.price}起送`
      else if (this.sPrice < this.data.price) return `还差${this.data.price - this.sPrice}起送`
      else return `去结算`
    }
  },
  methods: {
    beforeEnter (el) {

      // this.ballList.some(obj => {
      //   if (obj.show) {
      //     let pos = obj.el.getBoundingClientRect()
      //     let y = window.innerHeight - pos.top - 23
      //     let x = pos.left - 30
      //     el.display = ''

      //     el.style.transform = `translate3d(0,${-y}px,0)`;
      //     let inner = el.getElementsByClassName("inner")[0];
      //     inner.style.transform = `translate3d(${x}px,0,0)`;
      //   }
      // })，
      for (let i = 0; i < this.ballList.length; i++) {
        let ball = this.ballList[i];
        if (ball.show) {
          let pos = ball.el.getBoundingClientRect();
          let y = window.innerHeight - pos.top - 23;
          let x = pos.left - 30;
          el.display = "";
          el.style.transform = `translate3d(0,${-y}px,0)`;
          let inner = el.getElementsByClassName("inner")[0];
          inner.style.transform = `translate3d(${x}px,0,0)`;
        }
      }
    },
    enter (el, done) {
      el.offsetWidth;
      this.$nextTick(() => {
        el.style.transform = `translate3d(0,0,0)`;
        let inner = el.getElementsByClassName("inner")[0];
        inner.style.transform = `translate3d(0,0,0)`;
        el.addEventListener("transitionend", done);
      });
    },
    afterEnter (el) {
      el.style.display = "none";
      // 释放小球
      this.$store.commit("ball/removeBall");

    },
    delList () {
      this.$store.commit('storeList/delCart')
    },
    goBut (price) {
      if (price > this.data.price) this.$router.push({ path: '/buy', query: { price } })
      else console.log(price)

    }
  },
  components: {
    addCart

  },
  data () {
    return {
      flge: false
    }
  }
}
</script>

