<template>
  <div class="tag-box">
    <span class="tag"
          :class="{active:t.id==currentType}"
          v-for="t in tag"
          @click="chengTag(t.id)"
          :key="t.id">{{t.name}}({{t.count}})</span>
  </div>
</template>

<script>
export default {

  props: ["tag", "currentType"],

  methods: {
    chengTag (id) {
      this.$emit('change', id)

    }
  }

}
</script>

