<template>
  <div class="bsc">
    <div>
      <xheader :seller='seller'></xheader>
      <van-tabs :v-model="1"
                sticky
                color='ffda41'
                animated>
        <van-tab title="商家">
          <!-- 商家 -->
          <order></order>
        </van-tab>
        <van-tab title="详情">
          <!-- 详情 -->
          <comment></comment>
        </van-tab>
        <van-tab title="评价">

          <endtag :seller="seller"></endtag>
        </van-tab>

      </van-tabs>
    </div>
    <cart :data='seller'></cart>
  </div>
</template>

<script>
import xheader from './header'
import order from './orderList'
import Cart from './cart.vue'
import comment from './../comment'
import endtag from './end.vue'


import { GetStoreById } from '@/utils/api.js'
import BsScroll from 'better-scroll'



export default {
  data () {
    return {
      seller: [],
      BSscroll: null
    }
  },
  components: {
    xheader,
    order,
    Cart,
    comment,
    endtag
  },

  methods: {

    getSdata () {
      GetStoreById({ id: this.$route.query.id }).then(res => {
        this.seller = res.data
      })
    }
  },
  mounted () {
    this.BSscroll = new BsScroll('.bsc', {
      bounce: false,
      click: true,
    })
  },
  created () {
    this.getSdata()
  }
}
</script>

<style lang="scss" scoped>
.bsc {
  height: 100vh;
  overflow: hidden;
}
</style>