import Vue from "vue";
const ball = {
  namespaced: true,
  state: {
    ballList: [
      { show: false },
      { show: false },
      { show: false },
      { show: false },
      { show: false },
      { show: false },
    ],
    runBall: [],
  },
  mutations: {
    //   showBall(state, tage) {
    //     state.ballList.some((el) => {
    //       if (!el.show) {
    //         Vue.set(el, "show", true);
    //         Vue.set(el, "el", tage);
    //         state.runBall.push(el);
    //       }
    //       return;
    //     });
    //   },
    //   removeBall(state) {
    //     state.runBall.some((el) => {
    //       if (el.show) Vue.set(el, "show", false);
    //       return;
    //     });
    //   },
    showBall(state, el) {
      for (var i = 0; i < state.ballList.length; i++) {
        let ball = state.ballList[i];
        if (!ball.show) {
          Vue.set(ball, "show", true);
          Vue.set(ball, "el", el);
          state.runBall.push(ball);
          break;
        }
      }
    },
    removeBall(state) {
      let ball = state.runBall.shift();
      if (ball) {
        Vue.set(ball, "show", false);
      }
    },
  },
};
export default ball;