import { GetByNav } from "@/utils/api.js";
import Vue from "vue";
const storeList = {
  namespaced: true,
  state: {
    list: [],
  },
  getters: {
    // 选中的列表
    seller(state) {
      let seList = [];
      state.list.forEach((obj) => {
        obj.content.forEach((el) => {
          if (el.count) seList.push(el);
        });
      });
      return seList;
    },
    // 总价格
    sPrice(state, getters) {
      return getters.seller.reduce((price, el) => {
        return price + el.price * el.count;
      }, 0);
    },
    // 总数量
    Snum(state, getters) {
      return getters.seller.reduce((num, el) => {
        return num + el.count;
      }, 0);
    },
  },
  mutations: {
    saveList(state, arr) {
      state.list = [...arr];
    },
    addCart(state, { index, type }) {
      let el = state.list[index].content[type];
      if (el.count) Vue.set(el, "count", el.count + 1);
      else Vue.set(el, "count", 1);
    },
    subCart(state, { index, type }) {
      let el = state.list[index].content[type];
      if (el.count) Vue.set(el, "count", el.count - 1);
    },
    delCart(state) {
      state.list.forEach((item) => {
        item.content.forEach((prod) => {
          if (prod.count) prod.count = 0;
        });
      });
    },
  },
  actions: {
    // 请求列表
    getListData({ commit }, id) {
      return new Promise((reslove) => {
        GetByNav({ id }).then((res) => {
          let data = res.data.goods;
          data.forEach((item, index) => {
            item.content.forEach((pord, type) => {
              pord.index = index;
              pord.type = type;
            });
          });
          commit("saveList", data);
          reslove();
        });
      });
    },
  },
};
export default storeList;