<template>

  <div class="list">
    <i v-for="i in 5"
       :key="i"
       :class="showStar(i)"></i>

  </div>
</template>

<script>

export default {
  props: {
    score: {
      default: 5

    }
  },
  methods: {
    showStar (i) {
      // 如果i是整数，直接出现对应星星
      if (this.score % 1 === 0) { return this.score >= i ? 'icon-star-active' : 'icon-star' }
      else {
        // 分数大于当前循环出得i元素就是星星
        if (i < this.score) return 'icon-star-active';
        // 分数向上取整是等于循环的元素 半星
        else if (Math.ceil(this.score) == i) return 'icon-star-half'
        // 不大于就没🌟
        else if (i > this.score) return 'icon-star'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.list {
  display: inline-block;
  margin: 0 3px 0 0;
  vertical-align: middle;
  i {
    width: 12px;
    height: 12px;
    display: inline-block;
    line-height: 1.4;
    margin-right: 2px;
    background-size: 100%;
    &.icon-star {
      background-image: url(../assets/images/star.png);
    }
    &.icon-star-active {
      background-image: url(../assets/images/star-active.png);
    }
    &.icon-star-half {
      background-image: url(../assets/images/star-half.png);
    }
  }
}
</style>